<script>
    import IntersectionObserver from 'svelte-intersection-observer';
    import Video from './Video.svelte'
    export let src;
    export let id;
    export let captions;
    export let size;

    const layout = (size === 'small') ? 'col-text' : 'cover';
    const section = (size === 'small') ? 'col-text' : 'full chapter';

    let element;
    let intersecting;

</script>

<IntersectionObserver {element} bind:intersecting threshold=.5>
<section class='{section} interview {intersecting ? 'visible' : 'invisible'}' {id} bind:this={element}>
    <Video
        {src}
        {captions}
        {layout}
        controls = 'controls'
        scroll = false
    />
</section>
</IntersectionObserver>

<style>
    .interview {
        transition: opacity .5s;
        padding-top: 3rem;
        padding-bottom: 3rem;
    }
    .visible { opacity: 1; }
    .invisible { opacity: 0; }
</style>